import React, { Component } from 'react';
import { translate, withNamespaces } from 'react-i18next';
import DateRange from '@material-ui/icons/DateRange';
import Animated from './Animated';
import { getAllMonths } from '../utils';
import { toSnakeCase } from './Events';
export class Experience extends Component {
  componentDidMount() {
    this.props.getExpRequest();
  }
  render() {
    const { data, t } = this.props;

    return (
      data && (
        <Animated>
          {data.map(
            (item, key) =>
              item && (
                <div key={key} className="block">
                  <p>
                    <b>{t('position', 'Position')}: </b>
                    {t(
                      `Expirience:${toSnakeCase(item.position)}`,
                      item.position
                    )}{' '}
                    | <b>{t('company', 'Company')}: </b>
                    {t(`Expirience:${toSnakeCase(item.company)}`, item.company)}
                  </p>
                  <p className="date">
                    <span>
                      <DateRange />{' '}
                      <span>
                        {t(`Expirience:${toSnakeCase(item.from)}`, item.from)} -{' '}
                        {t(`Expirience:${toSnakeCase(item.to)}`, item.to)}.{' '}
                        {getAllMonths(
                          item.from,
                          item.to,
                          t('year', 'year(s)'),
                          t('month', 'month(s)')
                        )}
                      </span>
                    </span>
                  </p>
                  <p>
                    {t(
                      `Expirience:${toSnakeCase(item.company)}_responsibility`,
                      item.responsibility
                    )}
                  </p>
                </div>
              )
          )}
        </Animated>
      )
    );
  }
}

export default withNamespaces(['UI', 'Expirience'])(Experience);



